import React, { Component } from "react";
import {Navbar, NavbarBrand, Jumbotron,Nav, NavbarToggler, Collapse, NavItem, Button,Modal,ModalBody,ModalHeader,Form,FormGroup, Label, Input} from 'reactstrap';
import {NavLink} from 'react-router-dom';

class Header extends Component{
    constructor(props){
        super(props);
        this.state={
            isNavOpen:false,
            ismodalOpen:false
        };
        this.toggleNav=this.toggleNav.bind(this);
        this.toggleModal=this.toggleModal.bind(this);
        this.handleLogin=this.handleLogin.bind(this);
    }
    toggleNav(){
        this.setState({isNavOpen: !this.state.isNavOpen});
    }
    toggleModal(){
        this.setState({ismodalOpen: !this.state.ismodalOpen});
    }
    handleLogin(){
       this.toggleModal();
       alert("username:"+this.username.value + "password:"+this.password.value+"Remember:"+this.remember.checked);
      
    }
    render() {
        return (
            <React.Fragment>
                 <Navbar dark expand="md">
                          <div className="container">
                                    <NavbarToggler onClick={this.toggleNav}/>
                                <NavbarBrand className="mr-auto" href="/">
                                      <img src="assets/images/logo.png" height="30" width="41"/>
                                </NavbarBrand>
                                <Collapse isOpen={this.state.isNavOpen} navbar>
                                <Nav navbar>
                                        <NavItem>
                                            <NavLink className="nav-link" to="/home">
                                                <span className="fa fa-home fa-lg"></span> Home
                                            </NavLink>
                                        </NavItem>    
                                        <NavItem>
                                            <NavLink className="nav-link" to="/Aboutus">
                                                <span className="fa fa-info fa-lg"></span> About us
                                            </NavLink>
                                        </NavItem>     
                                        <NavItem>    
                                            <NavLink className="nav-link" to="/menu">
                                                <span className="fa fa-list fa-lg"></span> Menu
                                            </NavLink>
                                        </NavItem> 
                                        <NavItem>    
                                            <NavLink className="nav-link" to="/contactus">
                                                <span className="fa fa-address-card fa-lg"></span> Contact Us
                                            </NavLink>
                                        </NavItem>
                                </Nav>
                                <Nav className="ml-auto" navbar>
                                         <NavItem>
                                                <Button outline onClick={this.toggleModal}>
                                                       <span className="fa fa-sign-in fa-lg">Login</span>
                                                </Button>
                                         </NavItem>
                                </Nav>
                                </Collapse>
                         </div>
                     </Navbar>
                 <Jumbotron>
                        <div className="container">
                                <div className="row row-header">
                                        <div className="col-12 col-sm-6">
                                              <h1>Ristorant con fusion</h1>
                                              <p>We take inspiration from the World's best cuisines, and create a unique fusion experience. Our lipsmacking creations will tickle your culinary senses!</p>
                                        </div>
                                        <div className="col-12 col-sm-6 align-self-center">
                                               <Button className="btn btn-warning ml-5 btn-md">Reserve Table</Button>
                                        </div>
                                </div>
                        </div>
                 </Jumbotron>
                 <Modal isOpen={this.state.ismodalOpen} toggle={this.toggleModal}>
                        <ModalHeader toggle={this.toggleModal}>
                             Login
                        </ModalHeader>
                        <ModalBody>
                                  <Form onSubmit={this.handleLogin}>
                                           <FormGroup>
                                                  <Label htmlFor="username">Username</Label>
                                                  <Input type="text" id="username" name="username"
                                                  innerRef={(input)=> this.username=input}/>
                                           </FormGroup>
                                           <FormGroup>
                                                  <Label htmlFor="password">Password</Label>
                                                  <Input type="password" id="password" name="password"
                                                   innerRef={(password)=> this.password=password}/>
                                           </FormGroup>
                                           <FormGroup check>
                                           <Label check>
                                                 <Input type="checkbox" name="remember"  innerRef={(remember)=> this.remember=remember}/>remember me
                                             </Label>
                                           </FormGroup>
                                           <Button type="submit" value="submit" color="primary">Login</Button>
                                      </Form>    
                        </ModalBody>
                 </Modal>
            </React.Fragment>
        );
    }
}
export default Header;