
import React from 'react';
import { Card , CardImg,CardImgOverlay,CardTitle ,Breadcrumb, BreadcrumbItem, CardText, CardFooter, Button, Modal, ModalHeader, ModalBody} from 'reactstrap';
import {Link} from 'react-router-dom';
import {Loading } from './Loadingcomponent';
import {baseUrl} from '../shared/baseUrl';

  
 function RenderMenu({dish}){
     return(
      <Card>
         <Link to={`/menu/${dish.id}`}>        
            <CardImg width="100%" src={baseUrl + dish.image} alt={dish.name} />

             <CardImgOverlay>
                  <CardTitle>{dish.name}</CardTitle>
                 
                 
            </CardImgOverlay>
            <CardFooter>
                  <CardText>price:${dish.price}</CardText>
                  </CardFooter>
          </Link>
      </Card>
     )
 }
     
 const Menu= (props)=>{
                                
        const menu = props.dishes.dishes.map((dish) => {
          return (
              
            <div key={dish.id} className="col-12 col-md-5 m-2">
             <RenderMenu dish={dish} />
            </div>
          );
      });
    if(props.dishes.isLoading){

      return(
        <div className="container">
            <div className="row">
                  <Loading/>
            </div>
        </div>
      );
  }
  else if(props.dishes.errMess){
      return(
          <div className="container">
              <div className="row">
                 <h4>{props.dishes.errMess}</h4>
              </div>
          </div>
        );  
  }
  else
      return (
        <div className="container">
          <div className="row">
               <Breadcrumb>
                            <BreadcrumbItem><Link to='/home'>Home</Link></BreadcrumbItem>
                            <BreadcrumbItem active>Home</BreadcrumbItem>
                </Breadcrumb>            
                            <div className="col-12">
                                 <h3>Menu <span> <Button>Add Dish</Button> </span></h3>
                                  
                                 <hr />
                            </div>
                  <Modal>
                            <ModalHeader>
                                      Add your own dish by filling details
                            </ModalHeader>
                            <ModalBody>

                            </ModalBody>

                  </Modal>
              
          </div>
          <div className="row d-flex justify-content-center ">
             {menu}
            </div>
          </div>
      );
 }

export default Menu;  